.page-header {
  background-color: $background-header;
}

.page-header__inner {
  padding-top: 13px;
  padding-bottom: 80%;

  background-image: url("../img/bg-header-mobile.png");
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: left bottom;
}

.page-header__top {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  margin-bottom: 48px;
}

.page-header__phone {
  margin-top: 5px;

  font-size: 18px;
  line-height: 27px;
  text-decoration: none;
  color: $text-color;
}

.page-header__title {
  margin: 0;
  margin-bottom: 34px;

  font-size: 46px;
  font-weight: 800;
  line-height: 51px;
  color: $main-color;
}

.page-header__desc {
  margin: 0;
  margin-bottom: 35px;

  font-size: 24px;
  line-height: 32px;
}

@media (min-width: $desktop-breakpoint) {
  .page-header__inner {
    padding-top: 15px;
    padding-bottom: 114px;

    background-image: url("../img/bg-header-desktop.png");
    background-repeat: no-repeat;
    background-position: right bottom;
    background-size: auto;
  }

  .page-header__top {
    margin-bottom: 67px;
  }

  .page-header__phone {
    margin-top: 0;

    font-size: 22px;
    line-height: 33px;
  }

  .page-header__title {
    width: 796px;
    margin-bottom: 77px;

    font-size: 70px;
    line-height: 84px;
  }

  .page-header__desc {
    width: 668px;
    margin-bottom: 40px;

    font-size: 30px;
    line-height: 41px;
  }
}
