.page-footer {
  font-size: 16px;
  line-height: 24px;
  color: #a6a6a6;
}

.page-footer__inner {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding-bottom: 25px;
}

.page-footer__copyright {
  display: inline-block;
  margin: 0;
  margin-bottom: 10px;
}

.page-footer__privacy {
  text-decoration: none;
  color: #a6a6a6;
  border-bottom: 1px solid #a6a6a6;
}

@media (min-width: $desktop-breakpoint) {
  .page-footer {
    font-size: 18px;
    line-height: 27px;
  }

  .page-footer__inner {
    flex-direction: row;
    padding-bottom: 29px;
  }

  .page-footer__copyright {
    margin-right: 60px;
    margin-bottom: 0;
  }
}
