.slider__toggle {
  width: 20px;
  height: 16px;
  padding: 16px 10px;
  margin: -10px;
  border: 0;
  background-color: transparent;
  background-image: url(../img/arrow.svg);
  background-position: center;
  background-repeat: no-repeat;
  cursor: pointer;

  &--prev {
    transform: rotate(180deg);
  }
}

