.deals {
  padding-top: 40px;
  padding-bottom: 50px;

  font-size: 16px;
  line-height: 22px;
  background-color: #eeeeee;
}

.deals__heading {
  margin-bottom: 30px;
}

.deals__container {
  display: flex;
  flex-wrap: wrap;
  max-width: 980px;

  background-color: #ffffff;

  overflow: hidden;
}

.deals__list {
  display: flex;
  align-items: flex-start;
  order: 2;

  margin: 0;
  padding: 30px 15px 35px;

  list-style: none;
}

.deals__row {
  justify-content: flex-start;

  overflow: hidden;
}

.deals__column {
  width: 100%;
  margin-right: 39px;
}

.deals__title {
  margin: 0;
  margin-bottom: 10px;

  font-size: 22px;
  line-height: 29px;
}

.deals__debt {
  margin: 0;
  margin-bottom: 25px;

  font-size: 20px;
  line-height: 26px;
  color: #0838f1;
}

.deals__details {
  width: 100%;
  margin-bottom: 12px;

  font-size: 16px;
  line-height: 24px;

  border-collapse: collapse;
}

.deals__caption,
.deals__date {
  padding-top: 14px;
  padding-bottom: 6px;
  border-bottom: 1px solid #d3d3d5;
}

.deals__caption {
  font-weight: 400;
}

.deals__date {
  text-align: right;
  padding-left: 10px;
}

.deals__period {
  margin: 0;
  margin-bottom: 15px;
}

.deals__desc {
  margin-top: 0;
  margin-bottom: 25px;
}

.deals__creditors {
  margin-bottom: 20px;
}

.deals__preview {
  display: flex;
  margin: 0;
}

.deals__preview-img {
  display: block;
  width: 170px;
  margin-right: 20px;
}

.deals__preview-caption a {
  padding-bottom: 3px;

  text-decoration: none;
  color: #0838f1;

  border-bottom: 1px solid #0838f1;
}

.deals__toggles {
  display: flex;
  justify-content: space-between;
  align-items: center;
  order: 1;

  width: 100%;
  height: 48px;
  padding: 0 10px;
  margin: 0;

  background-color: #3e4146;

  z-index: 10;
}

.deals__counts {
  padding-top: 15px;
  padding-bottom: 10px;

  color: #f5f5f6;
}

@media (min-width: $desktop-breakpoint) {
  .deals {
    padding-top: 60px;
    padding-bottom: 90px;

    font-size: $text-size;
    line-height: $text-height;
  }

  .deals__list {
    padding: 40px 35px 60px;
  }

  .deals__column {
    width: auto;
    margin-right: 0;

    &--left {
      width: 462px;
      margin-right: 87px;
    }

    &--right {
      width: 361px;
    }
  }

  .deals__title {
    font-size: 25px;
    line-height: 35px;
  }

  .deals__debt {
    margin-bottom: 11px;

    font-size: 25px;
    line-height: 35px;
  }

  .deals__details {
    margin-bottom: 16px;

    font-size: 20px;
    line-height: 28px;
  }

  .deals__creditors {
    margin-bottom: 20px;
  }

  .deals__preview {
    flex-direction: column;
  }

  .deals__preview-img {
    margin-right: 0;
    margin-bottom: 26px;
  }
}
