.logo {
  display: flex;
  align-items: flex-end;
  text-decoration: none;
}

.logo__title {
  display: none;
}

@media (min-width: $desktop-breakpoint) {
  .logo__img {
    margin-right: 39px;
  }

  .logo__title {
    position: relative;

    display: block;
    width: 284px;
    margin: 0;
    padding-bottom: 4px;
    padding-left: 18px;

    font-size: 22px;
    font-weight: 400;
    line-height: 30px;
    color: #000000;

    &:before {
      content: "";

      position: absolute;
      bottom: 6px;
      left: 0;

      width: 2px;
      height: 52px;

      background-color: #0e1875;
    }
  }
}
