.contacts__inner {
  padding-top: 40px;
  padding-bottom: 40px;
}

.contacts__heading {
  margin-bottom: 20px;
}

.contacts__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;

  list-style: none;
}

.contacts__item {
  width: 100%;
  margin-bottom: 30px;
}

.contacts__city {
  margin: 0;
  margin-bottom: 10px;

  font-weight: 700;
}

.contacts__email {
  margin-bottom: 8px;

  text-decoration: none;
  color: $text-color;
}

.contacts__address {
  font-style: normal;
}

@media (min-width: $desktop-breakpoint) {
  .contacts__inner {
    padding-top: 60px;
    padding-bottom: 78px;
  }

  .contacts__heading {
    margin-bottom: 40px;
  }

  .contacts__item {
    width: 290px;
    margin-bottom: 42px;
    padding-right: 60px;
  }

  .contacts__city {
    font-size: 25px;
    line-height: 35px;
  }
}
